:root {
  --gray: #50514f;
  --red: #f25f5c;
  --yellow: #ffe066;
  --blue: #247ba0;
  --green: #70c1b3;
  --letter-done: #04724d;
  --letter-wrong: #9e2a2b;
  --configuration: #363635;
  --configuration-buttons: #a5a3a3;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--gray) !important;
}

.pacifico-regular {
  font-family: "Pacifico", cursive;
  font-weight: 400;
  font-style: normal;
}

#root {
  width: 100%;
  display: flex;
  justify-content: center;
}

.App {
  width: 75%;
}

.menu {
  color: lightgray;
  margin-bottom: 5vh;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.configuration-bar {
  margin-bottom: 5vh;
  background-color: var(--configuration);
  border-radius: 10px;
  padding: 5px 10px 5px 20px;
  display: flex;
  justify-content: space-between;
  -webkit-box-shadow: 0px 5px 10px 0px rgba(54, 54, 53, 1);
  -moz-box-shadow: 0px 5px 10px 0px rgba(54, 54, 53, 1);
  box-shadow: 0px 5px 10px 0px rgba(54, 54, 53, 1);
}

.configuration-bar-buttons{
	display: flex;
	justify-content: center;
	align-items: center;
	color: grey;
	font-weight: bold;
	gap: 10px;
}

.configuration-bar-buttons-button {
  background: none;
  border: none;
  cursor: pointer;
  opacity: 70%;
  transition: 0.3s ease-in;
  &.configuration-bar-buttons-button:hover {
    transform: scale(1.1);
    opacity: 100%;
  }
}

#game {
  width: 75%;
  margin: auto;
}

#text {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  font-family: 'Courier New', Courier, monospace !important;
}

#input {
  position: absolute;
  left: 0;
  top:0;
  opacity: 0;
}

.counters {
  display: flex;
  font-size: 32px;
  justify-content: space-between;
}

.counters time{
  color: var(--configuration-buttons);
}

.word-counters{
	display: flex;
	gap: 10px;
}

#key-counter {
  color: var(--green);
}

#errors-counter {
  color: var(--letter-wrong);
}

.letter-done {
  color: var(--green);
}

.letter-wrong {
  color: var(--letter-wrong);
}

.word {
  font-size: 24px;
  color: gray;
}

.missed-word {
  border-bottom: 1px solid var(--letter-wrong);
}

.letter {
  position: relative;
  &.active::before {
    content: '|';
    position: absolute;
    left: -50%;
    animation: 1s blink infinite ease-in-out;
  }
  &.active.is-last::before {
    content: '|';
    position: absolute;
    left: 60%;
    animation: 1s blink infinite ease-in-out;
  }
}

@keyframes blink {
  0%,
  25% {
    opacity: 1;
  }

  75% {
    opacity: 0;
  }
}

.restart-wrapper{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.restart-button button{
  background: none;
  outline: 0;
  border: 0;
  cursor: pointer;
  opacity: 70%;
  padding: 10px 20px;
  border-radius: 10px;
  transition: 0.3s all ease-in;
}

.restart-button button:hover{
  opacity: 100%;
  background-color: var(--configuration);
}
